import home from '../views/basic/index.vue'
import fullpage from '../views/fullpage/index.vue'
Vue.use(VueRouter)

const routes = [
	{
		path: '/', component: () => import('../views/index/index.vue'),
		children: [
			{ path: '/', redirect: '/component' },
			{ path: '/component', component: home,
				children: [
					{ path: '/', component: () => import('../views/index/readMe.vue') },
					{ path: 'device', component: () => import('../views/component/device/index.vue') }
				]
			},
			{ path: '/basic', component: home,
				children: [
          { path: '/', component: () => import('../views/basic/string/index.vue') },
          { path: 'closure', component: () => import('../views/basic/closure/index.vue') },
          { path: 'width-height', component: () => import('../views/basic/width-height/index.vue') },
          { path: 'transition-animation', component: () => import('../views/basic/transition-animation/index.vue') },
          { path: 'meta', component: () => import('../views/basic/meta/index.vue') },
          { path: 'link', component: () => import('../views/basic/link/index.vue') }
				]
			},

			{ path: '/demo', component: home,
				children: [
					{ path: '/', component: () => import('../views/demo/echarts/index.vue') },
					{ path: 'drag', component: () => import('../views/demo/drag/index.vue') },
					{ path: '3D-cube-animation', component: () => import('../views/demo/3D-cube-animation/index.vue') }
				]
			},

			{ path: '/fullpage', component: fullpage,
				children: [
					{ path: '/', component: () => import('../views/fullpage/fullpage-test/index.vue') },
				]
			}
		]
	}
]

routes.push({path: '*', redirect: '/basic' })

const router = new VueRouter({
	routes
})

export default router
